<script>
  import axios from 'axios'
  export default{
    data(){
      return {
        phaseNumber: '',
        data: [],
        orderId: 1
      }
    },
    watch: {
      // 
      '$route': 'fetchData'
    },
    mounted: function () {
      this.fetchData()

    },
    methods: {
      fetchData(){
        this.phaseNumber = this.$route.params.phaseNumber
        this.getPhase()
      },
      getPhase(){
        var url = basePath + "/fitOrder/notEditWorkAmount/order/" + this.orderId + "/phase/" + this.phaseNumber;
        axios.get(url
        ).then(response => (
          this.data = response.data,
            this.data.forEach((seq)=>{
              seq.processFlow=JSON.parse(seq.processFlow)
              if(typeof seq.show=='undefined')
                this.$set(seq,'show','none')
            })
        ))

      },
      showList(fitSequence){
        if(fitSequence.show=='none')
          fitSequence.show='block'
        else
          fitSequence.show='none'
      },
      deleteTask(id){
          if(confirm(''))
          {
        let url = basePath + "/fitOrder/deleteFitTaskInstance/" + id;
        axios.delete(url).then(
            response=>(
                console.log(response.data)
            )

        )}
      }
    }

  }
</script>



